.works {
    min-height: 100vh;
    background-color: #e2c7ac;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    min-width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border: 1px solid;
}

.title {
    height: 130px;
}

.title p {
    background-color: chocolate;
    margin: 0px;
}

.workItems {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: 1px solid;
    flex-wrap: wrap;
}